import React from 'react'
import Masonry from 'react-smart-masonry';
import '../Styles/Editorial.scss';


export default function editorial() {
  return (
    <section className='editorial'>
      <div className="container">
        <div className="editorial__selected selected">
          <div className="selected__image-wrapper">
            <img src={"https://picsum.photos/400/320"} alt="" className="selected__image" />
          </div>
          <h2 className="selected__title">
            Lorem ipsum dolor sit.
          </h2>
        </div>
        <div className="editorial__bottom-block">
          <div className="editorial__controls">
            <button className="editorial__button editorial__left-button">Previous</button>
            <button className="editorial__button editorial__right-button">Next</button>
          </div>
          <Masonry 
          className="editorial__image-block masonry-block"
          columns={{tablet: 2, desktop: 3}}
          breakpoints={{tablet: 0, desktop: 767}} 
          gap={10}
          autoArrange={true}
          >
            <img height={640} width={400} src={"https://picsum.photos/200/320"} className="editorial__image masonry-block__item"  alt="" />
            <img height={620} width={400} src={"https://picsum.photos/200/310"} className="editorial__image masonry-block__item"  alt="" />
            <img height={800} width={400} src={"https://picsum.photos/200/400"} className="editorial__image masonry-block__item"  alt="" />
            <img height={760} width={400} src={"https://picsum.photos/200/380"} className="editorial__image masonry-block__item"  alt="" />
            <img height={1040} width={400} src={"https://picsum.photos/200/520"} className="editorial__image masonry-block__item"  alt="" />
            <img height={280} width={400} src={"https://picsum.photos/200/140"} className="editorial__image masonry-block__item"  alt="" />
            <img height={440} width={400} src={"https://picsum.photos/200/220"} className="editorial__image masonry-block__item"  alt="" />
            <img height={520} width={400} src={"https://picsum.photos/200/260"} className="editorial__image masonry-block__item"  alt="" />
          </Masonry>
        </div>
      </div>
    </section>
  )
}
