import React, { useState, useMemo, useRef, useEffect } from 'react';
import { ref, uploadBytes, getDownloadURL, deleteObject } from "firebase/storage";
import { ref as refDb, get, set, remove as removeDb} from "firebase/database";
import { uid } from "uid";
import "../Styles/Upload.scss";
import { imageListClasses } from '@mui/material';

let databaseURL;
let articleName;

export default function Upload(props) {
  const storage = props.storage;
  const database = props.database;

  const [image, setImage] = useState(null);
  
  const [value, setValue] = useState(0);

  const [main, setMain] = useState([]);
  const [editorial, setEditorial] = useState([]);

  const select = useRef(null);
  const articleInput = useRef(null);

  useEffect(()=>{
    updateUrl();
    updateArticleName();
  },[])
  
  useMemo(()=>{
    get(refDb(database, `/`)).then(promise=>{
      if(promise.val()){
        setMain(promise.val().Main);
      }
    });
  }, [value])

  const addToDatabase = (imageUrl, databaseURL, name) => {
    const databaseRef = refDb(database, `${databaseURL}/${name}`);
    set( databaseRef, {
      image: imageUrl,
      name: name
    })
  }
  
  const uploadImage = () => {
    if(!image){
      alert("Choose image");
      return;
    };
    if(!articleName){
      alert("Choose name");
      return;
    }
    if(Object.values(main).find((item)=>{return articleName === item.name})){
      alert("Name was already taken")
      return;
    }
    if(!Array.from(select.current.children).find(item=>{
      return item.checked;
    })){
      alert("Choose direction");
      return;
    }
    const imageRef = ref(storage, `Images/${articleName}`);
    uploadBytes(imageRef, image).then(()=>{
      getDownloadURL(imageRef).then(image=>{
        addToDatabase(image, databaseURL, articleName);
        alert("Image uploaded");
        setValue(value + 1);
      });
    });
  }

  const deleteImage = (refDb, ref)=>{
    removeDb(refDb);
    deleteObject(ref)
  }
  
  const updateUrl = () => {
    const selectedInput = Array.from(select.current.children).find(item=>{
      return item.checked;
    });
    if(!selectedInput) return;
    databaseURL = "/" + Array.from(select.current.children).find(item=>{
      return item.checked;
    }).value;
  }
  const updateArticleName = () => {
    articleName = articleInput.current.value;
  }

  let imagesList;
  if(!main || main.length === 0){
      imagesList = <>Images were not found</>
  }
  else{
      imagesList = Object.entries(main).map((item, index)=>{
        return (
      <li data-value={value} className='upload__item item' key={index} >
        <img className='item__image' src={item[1].image} alt="" />
        <p className='item__name'>{item[1].name}</p>
        <button className='item__button' onClick={(e)=>{
          deleteImage(
            refDb(database, `/Main/${item[0]}`),
            ref(storage, `/Images/${item[0]}`)
          )
          e.target.closest(".item").remove();
          }}>Delete</button>
      </li>
      )
    })
  }
  
  let imageName;
  if(!image){
    imageName = <>Image not selected</>
  }
  else{
    imageName = image.name;
  }

  return (
    <section className='upload'>
      <label className='upload__image-label' htmlFor="image-upload">
        Select image
      </label>
      <span className='upload__selected-image'>
      {
        imageName
      }
      </span>
      <input className='upload__image-input' onChange={(e) => { setImage(e.target.files[0])}} type="file" name="image-upload" id="image-upload" accept="image/png, image/jpg, image/gif, image/jpeg" />
      <div className="upload__text-block">
        <p className='upload__text'>Article name:</p>
        <input className='upload__text-input' onChange={()=>{updateArticleName()}} ref={articleInput} type="text" name="article-name" id="article-name" />
      </div>
      <div onClick={(e) => {
    if(e.target.closest(".upload__select")){
      updateUrl();
    }}} className="upload__select" ref={select}>
        <input type="radio" value="Main" name="select" id="select-main" />
        <label htmlFor="select-main">
          Main
        </label>
        <input type="radio" value="Portraits" name="select" id="select-portraits" />
        <label htmlFor="select-portraits">
          Portraits
        </label>
        <input type="radio" value="Editorial" name="select" id="select-editorial" />
        <label htmlFor="select-editorial">
          Editorial
        </label>
      </div>
      <button className='upload__submit-button' onClick={()=>{ uploadImage() }}>Upload</button>
      <ul className='upload__list'>
        {
          imagesList
        }
      </ul>
    </section>
  )
}
