import React, {useState, useEffect, useMemo} from 'react'
import '../Styles/Main.scss'
import Masonry from 'react-smart-masonry';
import { ref, get } from "firebase/database";

let selectedImageId = 0;

export default function Main(props) {
  
  const database = props.database;
  const databaseURL = "/Main";
  
  let [urls, setUrls] = useState([]);

  useMemo(()=>{
    get(ref(database, `${databaseURL}`)).then(promise=>{
      if(!promise.val()) return;
      if(Object.values(promise.val())){
        const imagesArray = Object.values(promise.val());
        setUrls(imagesArray);
      }
    });
  },[])
  
  const [imageSrc, setImageSrc] = useState(Object.values(urls)[0]);
  
  function selectImage(src, index){
    setImageSrc(src);
    selectedImageId = index;
    window.scrollTo({top: 0, behavior: "smooth"});
  }
  
  function selectNext(){
    if(urls.length === 0) return;
    if(selectedImageId >= Object.values(urls).length - 1){
      selectedImageId = -1;
    }
    selectImage(Object.values(urls)[selectedImageId + 1].image, ++selectedImageId);
  }

  function selectPrev(){
    if(urls.length === 0) return;
    if(selectedImageId <= 0){
      selectedImageId = Object.values(urls).length;
    }
    selectImage(Object.values(urls)[selectedImageId - 1].image, --selectedImageId);
  }

  useEffect(()=>{
    window.dispatchEvent(new Event('resize'));
  },[])
  
  useEffect(()=>{
    if(urls.length !== 0){
      setImageSrc(urls[selectedImageId].image);
    }
  },[urls])
  
  let imagesList;
  if(urls.length === 0){
    imagesList = <>Images were not found</>
  }
  else{
    imagesList = urls.map((url, index)=>{
      return <img key={index} onClick={()=>{selectImage(url.image, index)}} width={200} height={400} src={url.image} className="main__image masonry-block__item"  alt="" />
    });
  }

  return (
    <section className='main'>
      <div className="container">
        <div className="main__selected selected">
          <div className="selected__image-wrapper">
            <img src={imageSrc} alt="" className="selected__image" />
          </div>
        </div>
        <div className="main__bottom-block">
          <div className="main__controls">
            <button onClick={()=>{selectPrev()}} className="main__button main__left-button">Previous</button>
            <button onClick={()=>{selectNext()}} className="main__button main__right-button">Next</button>
          </div>
          <Masonry 
          className="main__image-block masonry-block"
          columns={{tablet: 2, desktop: 3}}
          breakpoints={{tablet: 0, desktop: 767}} 
          gap={10}
          autoArrange={true}
          >
          {
            imagesList
          }
          </Masonry>
        </div>
      </div>
    </section>
  )
}
